@use 'global' as *;

.p-recruit__inner.l-inner {
  padding: 0;
  @include mq(md){
    padding-left: rem(20);
    padding-right: rem(20);
  }
}

.p-recruit__text-block {
  text-align: center;
  margin-top: rem(30);
}

.p-recruit__text {
  text-align: center;
  font-size: rem(14);
  font-weight: 300;
  letter-spacing: 0.05em;
  line-height: 1.79;
}

.p-recruit__wrapper {
  @include mq(md){
    display: flex;
    flex-direction: row-reverse;
  }
}

.p-recruit__contents {
  @include mq(md){
    width: 40%;
  }
}

.p-recruit__img {
  position: relative;
  @include mq(md){
    width: 60%;
  }
}

.p-recruit__img::before {
  content: "";
  display: block;
  padding-top: 37.3333333%;
}

.p-recruit__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.p-recruit__contents {
  background-color: #f7f7f7;
  padding-top: rem(60);
  padding-bottom: rem(50);
}

.p-recruit__btn {
  text-align: center;
  margin-top: rem(30);
}
